<style>
  h1 {
    font-size: 100px;
    font-weight: 500;
    -webkit-text-fill-color: transparent;
    background: linear-gradient(
      180deg,
      rgb(255 255 255) 0%,
      rgb(172 240 255) 20%,
      rgb(146 231 249) 60%,
      rgb(255 255 255) 100%
    );
    -webkit-background-clip: text;
    animation-name: blur;
    animation-duration: 3s;
    transition: all 0.3s ease-in-out;
    /* filter: hue-rotate(41deg); */
  }

  @keyframes blur {
    from {
      filter: blur(5px);
      opacity: 0.1;
    }
    to {
      filter: blur(0px);
      opacity: 1;
    }
  }

  .scroll {
    opacity: 0.1;
    transition: all 1s ease-in-out;
  }

  main {
    height: 100vh;
    width: 100vw;
    overflow-x: hidden;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    background: linear-gradient(
        160deg,
        rgb(250 253 255) 0%,
        rgb(203 241 250) 50%,
        rgb(240, 240, 240) 100%
      )
      fixed;
    background-repeat: no-repeat;
    background-attachment: local;
  }
  section {
    width: 80%;
    max-width: 1000px;
    margin: auto;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3f4040;
    z-index: 10;
    position: relative;
  }
  p {
    font-size: 34px;
    font-weight: 400;
  }
  .btn {
    font-size: 48px;
    margin: auto;
    border-radius: 5px;
    box-shadow: 0 1px 15px 2px rgb(0 0 0 / 5%);
    background: rgba(255, 255, 255, 0.5);
    padding: 50px 0;
    width: 80%;
    border-radius: 20px;
    background: white;
    color: var(--color-accent);
    transition: all 0.15s ease-in-out;
    /* border: 1px solid #2196f352; */
  }
  .btn:hover {
    filter: brightness(0.99);
    transform: scale(1.03);
  }
  .btn-close {
    position: fixed;
    top: 5%;
    right: 5%;
    color: var(--color-accent);
  }
  .svg-holder {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 0;
  }
  svg {
    max-width: 350px;
    transition: all 0.3s linear;
    overflow: visible;
  }

  @keyframes blink {
    0% {
      opacity: 0.1;
    }
    33% {
      opacity: 1;
    }
    66% {
      opacity: 0.1;
    }
    100% {
      opacity: 0.1;
    }
  }
  @keyframes blink2 {
    0% {
      opacity: 0.1;
    }
    33% {
      opacity: 0.1;
    }
    66% {
      opacity: 1;
    }
    100% {
      opacity: 0.1;
    }
  }
  @keyframes blink3 {
    0% {
      opacity: 0.1;
    }
    33% {
      opacity: 0.1;
    }
    66% {
      opacity: 0.1;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.02);
    }
    100% {
      transform: scale(1);
    }
  }

  g {
    transition: all 0.3s ease-in-out;
  }
  .svg-holder.animate #square {
    animation: blink 5s infinite;
  }
  .svg-holder.animate #circle {
    animation: blink2 5s infinite;
  }
  .svg-holder.animate #triange {
    animation: blink3 5s infinite;
  }
  .svg-holder.animate #squiggles,
  .svg-holder.animate #mag {
    transform-origin: center;
    animation: pulse 2s infinite;
  }
  .svg-holder.blur #squiggles,
  .svg-holder.blur #mag {
    transform-origin: center;
    animation: pulse 3s infinite;
  }

  .svg-holder #bigshape {
    transition: all 0.3s linear;
    transform-origin: left;
  }

  .svg-holder.blur #bigshape {
    filter: blur(7px);
  }
  .svg-holder.iso #bigshape {
    filter: blur(0px);
  }
  .svg-holder.iso #squiggles,
  .svg-holder.iso #mag {
    opacity: 0;
  }

  #square,
  #triange,
  #circle {
    opacity: 1;
  }
  #smallshape {
    display: none;
  }

  @media screen and (max-width: 900px) {
    h1 {
      background: none;
      -webkit-text-fill-color: inherit;
      font-size: 70px;
      text-align: center;
      color: var(--color-accent);
    }
    section {
      width: 80%;
    }
    p {
      font-size: 28px;
    }
    .btn {
      width: 100%;
      font-size: 30px;
      padding: 30px 20px;
      border-radius: 8px;
    }
    .btn-close {
      font-size: 14px;
    }
    svg {
      max-width: 75%;
    }
  }
</style>
